export default {
	name: "search",
	data() {
		return {
			curIndex: 0,
			
		}
	},
	props: {
		typeList: {
			type:Array,
			default(){
				return []
			}
			}
	},
	
	computed: {
		typeName(){
			return this.typeList.length?this.typeList[this.curIndex].name:''
		}
	},
	watch: {

	},
	methods: {
		changeType(res) {
			// picker选择结果的索引值
			this.curIndex = parseInt(res.detail.value)
			
			let typeId = this.typeList[this.curIndex]['type']
			this.$emit("changeType", typeId)
		}
	}
}
